ound-repeat: no-repeat;\n  background-size: cover;\n  height: 100vh;\n  width: 100%;\n}\n\n.login-view {\n  background-image: url('.././images/resort.jpg');\n}\n\n.login-container {\n  min-width: 350px;\n  max-width: 450px;\n  min-height: 170px;\n  height: 18vh;\n  max-height: 230px;\n  border-radius: 25px;\n  border: solid rgb(255, 255, 255, 0.1);\n  background-color: rgba(254, 253, 255, 0.25);\n  backdrop-filter: blur(5px);\n  position: absolute;\n  transform: translate(-50%, -50%);\n  margin: 50vh 50vw 0;\n  font-size: 20px;\n  color: rgb(30, 74, 120);\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n\n.option-container {\n  margin-top: 20px;\n  width: 65%;\n  display: flex;\n  align-items: center;\n  justify-content: space-evenly;\n}\n\n.individual-option {\n  display: flex;\n  align-items: center;\n}\n\n.inputs {\n  margin-top: 20px;\n  display: flex;\n  flex-direction: column;\n  width: 100%;\n  align-items: center;\n  overflow-x:hidden;\n}\n\n.inputs input {\n  border-radius: 7px;\n  border-color: rgba(30, 74, 120, 0.335);\n  background-color: rgba(255, 255, 255, 0.5);\n}\n\n.input-container {\n  max-width: 13vw;\n  display: flex;\n  margin-top: 10px;\n  justify-content: center;\n  width: 70%;\n  align-items: center;\n}\n\n.input-label {\n  margin-top: 0;\n  margin-bottom: 0;\n  margin-right: 5px;\n}\n\n.login-feedback {\n  margin-top: 10px;\n  margin-bottom: 0;\n  color: red;\n  font-size: 14px;\n}\n\n.login-button {\n  margin-top: 10px;\n  height: 4vh;\n  min-height: 25px;\n  width: 6vw;\n  min-width: 55px;\n  background-color: #81ecff2f;\n  border-radius: 8px;\n}\n\n.active-login:hover {\n  background-color: #81ecff;\n  cursor: pointer;\n}\n\n/* website view */\n.website-view {\n  background-image: url('.././images/resort-2.jpg');\n}\n\n.overlay {\n  height: 100%;\n  width: 100%;\n  backdrop-filter: blur(10px);\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n\n.website-nav {\n  background-color: rgba(255, 255, 255, 0.5);\n  height: 14%;\n  width: 100%;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n.nav-text-container {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  width: 95%;\n  height: 100%;\n}\n\n.title-container {\n  font-size: 20px;\n  color: rgba(0, 0, 0, 0.5);\n  width: 14%;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: space-evenly;\n  text-align: center;\n}\n\n.title-container p,h1 {\n  margin: 0;\n}\n\n.toggle-container {\n  display: flex;\n  width: 25vw;\n  height: 60%;\n  justify-content: space-evenly;\n  align-items: center;\n  border-radius: 25px;\n  background-color: rgba(255, 255, 255, 0.5);\n  min-width: 230px\n}\n\n.bookings-button {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  opacity: 1;\n  width: 45%;\n  height: 60%;\n  border-radius: 25px;\n  border: solid 2px #ffffff00;\n}\n\n.selected-booking {\n  background-color: #ffffff;\n}\n\n.unselected-booking {\n  transition: 0.2s;\n}\n\n.unselected-booking:hover {\n  border: solid 2px #ffffff;\n  cursor: pointer;\n}\n\n/* Display area */\n.display-area {\n  width: 100%;\n  height: 85%;\n}\n\n.main-view {\n  height: 100%;\n  width: 100%;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n\n.list-bookings-ribbon {\n  align-items: center;\n  margin-top: 10px;\n  display: flex;\n  justify-content: center;\n  width: 100%;\n  height: 13%;\n  position: relative;\n}\n\n.choose-booking-list {\n  margin-top: 10px;\n  display: flex;\n  width: 30vw;\n  height: 60%;\n  min-height: 25px;\n  justify-content: space-evenly;\n  align-items: center;\n  border-radius: 25px;\n  background-color: rgba(255, 255, 255, 0.5);\n  min-width: 230px\n}\n\n.booking-choice-container {\n  margin-top:0;\n  width: 80%;\n  height: 80%;\n  display: flex;\n  align-items: center;\n}\n\n.list-button {\n  width: 100%;\n  height: 100%;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  font-size: 0.9em;\n  opacity: 1;\n  text-align: center;\n  border-radius: 25px;\n  border: solid 2px #ffffff00;\n}\n\n.selected-button {\n  background-color: rgba(255, 255, 255, 0.7);\n}\n\n.unselected-button {\n  transition: 0.2s;\n}\n\n.unselected-button:hover {\n  border: solid 2px #ffffff;\n  cursor: pointer;\n}\n\n.cost-container {\n  position: absolute;\n  right: 10px;\n  height: 100%;\n  width: 20%;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  background-color: rgba(255, 255, 255, 0.5);\n  color: rgba(0, 0, 0, 0.593);\n  border-radius: 12px;\n}\n\n.total-spend {\n  margin:0;\n  opacity: 0.85;\n  font-size: 2vw\n}\n\n/* Booking list grid */\n.display-grid {\n  margin-top: 10px;\n  width: 100%;\n  height: 94%;\n  display: flex;\n  justify-content: space-evenly;\n  overflow-y: scroll;\n}\n\n.display-grid h2 {\n  opacity: 0.5;\n}\n\n.display-grid::-webkit-scrollbar {\n  display: none;\n}\n\n.booking-column {\n  width: 40vw;\n  min-width: 300px;\n  height: 100%;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n\n.current-booking-card {\n  margin-top: 12px;\n  width: 95%;\n  min-height: 30vw;\n  background-color: rgba(255, 255, 255, 0.10);\n  border: 2px solid rgba(255, 255, 255, 0.5);\n  border-radius: 13px;\n  overflow: hidden;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: space-between;\n}\n\n.card-img-container {\n  width: 100%;\n  height: 90%;\n  overflow: hidden;\n  position: relative;\n}\n\n.card-img {\n  background-position: bottom;\n  background-repeat: no-repeat;\n  background-size: cover;\n  height: 100%;\n  width: 100%;\n  transition: 0.2s;\n}\n\n.card-info {\n  position: absolute;\n  top: 0;\n  height: 90%;\n  width: 90%;\n  left: -100%;\n  backdrop-filter: blur(9px);\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  transition: 1s;\n  border-radius: 12px;\n  border: 2px solid rgba(255, 255, 255, 0.183);\n}\n\n.card-info p {\n  margin-top: 2px;\n  margin-bottom: 0;\n  font-size: 1.7vw;\n  opacity: 0.4;\n}\n\n.display-info {\n  left: 0;\n}\n\n.display-info:hover {\n  cursor: pointer;\n}\n\n.current-booking-card:hover .card-img {\n  transform: scale(1.03);\n}\n\n.current-booking-card:hover {\n  cursor: pointer;\n}\n\n.current-booking-card-text {\n  height: 10%;\n  display: flex;\n  width: 90%;\n  justify-content: space-between;\n  align-items: center;\n  overflow: hidden;\n  color: rgba(0, 0, 0, 0.6);\n}\n\n.current-booking-card-text p {\n  margin: 0;\n  font-size: 2vw;\n  opacity: 0.7;\n}\n\n.upcoming-booking {\n  color: green;\n}\n\n.past-booking {\n  color: red;\n}\n\n/* New bookings */\n\n.icon {\n  min-width: 25px;\n  min-height: 25px;\n  max-height: 40%;\n  transition: 0.4s;\n  opacity: 0.6;\n}\n\n.icon:hover {\n  cursor: pointer;\n  opacity: 1;\n  transform: translateY(-3px);\n}\n\n.new-bookings-ribbon {\n  width: 60%;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\n.new-bookings-ribbon-element {\n  margin-top: 20px;\n  height: 4vw;\n  min-height: 40px;\n  max-height: 50px;\n  display: flex;\n  justify-content: space-around;\n  align-items: center;\n  border-radius: 20px;\n}\n\n.calendar-icon-container {\n  height: 100%;\n  width: 22%;\n  display: flex;\n  justify-content: space-evenly;\n  align-items: center;\n}\n\n.right-filter-icon-container {\n  display: flex;\n  justify-content: space-evenly;\n  align-items: center;\n  height: 100%;\n  width: 22%;\n}\n\n.dropdown {\n  max-width: 80%;\n  color: rgba(0, 0, 0, 0.5);\n}\n\n.filter-label {\n  color: rgba(0, 0, 0, 0.5);\n}\n\n.new-bookings-display {\n  color: rgba(0, 0, 0, 0.45);\n  font-size: 25px;\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  text-align: center;\n}\n\n.new-bookings-display p {\n  margin: 2px;\n}\n\n@keyframes fade-in-out{\n  0% {\n    opacity: 0;\n  }\n  10% {\n    opacity: 1;\n  }\n  80% {\n   